@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    overflow-x: hidden;
}
.conatiner{
    
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
#quote-box{
   font-family: 'Raleway', sans-serif;
   background-color: #fff;
   padding:2rem 3rem;
   height:auto;
   max-width:450px;
   width:100%;
   border-radius: 3px;

  

}
.quote-logo{
    
    margin-right: 5px;
    font-size: 1.6rem;
}
#text{
margin-bottom: 1rem;
line-height: 1.9rem;
word-wrap: wrap;

text-align: center;
font-weight: 500;
font-size: 1.75rem;
}
#author{
    text-align: right;
margin-bottom: 1rem;



}
.btn-conatiner{
    display: flex;
    justify-content:space-between;
}
.btn{
   
    padding:.1rem .4rem;
   
    color: #fff;
    border-radius: 3px;
    border:none;
    cursor: pointer;
    user-select: none;
}
#tweet-quote{
    font-size: 1.5rem;
}
h4{
    color:white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
